@import "../scss/variables";

@media (min-width: 1025px) {
.boxed{
  .page {
    max-width: 1170px;
    margin: 0 auto;
    background: $background;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px, rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
  }

  body {
    background: $background !important;
  }

  .main-navbar.sticky.sticky-pin {
    width: 1170px;
  }

  .horizontalMenu > .horizontalMenu-list > li > a .fa {
    margin-right: 0px !important;
  }

  .navbar {
    max-width: 1170px;
    margin: 0 auto;
  }

  .vh {
    height: 100vh;
  }

  .navbar.land-navbar {
    max-width: 1170px;
    margin: 0 auto;
  }

  .app-sidebar {
    left: auto !important;
  }

  .app .main-header.side-header {
    left: auto !important;
    right: auto !important;
    width: 1170px !important;
    background: #fff;
  }
 .app.sidebar-mini.sidenav-toggled4 .first-sidemenu{
		left: auto !important;
   }
  .second-sidemenu {
    left: auto !important;
    margin-left: 100px;
  }

  .main-body {
    display: inherit;
  }

  .main-sidebar.side-menu {
    left: auto;
  }

  .main-header.side-header {
    left: auto !important;
    right: auto !important;
	position: sticky !important;
  }

  .main-sidebar-hide .side-menu {
    left: auto !important;
  }

  .modal-open .main-sidebar {
    z-index: 999;
  }
}
}

@media (min-width: 1025px){
.boxed.main-body {
    display: inherit;
  }
}
@media (min-width: 1025px){
body.boxed {
    background: #f2f3f9 !important;
  }
}